.Title {
  text-align: center;
  
}

.App-logo {
  height: 12vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.Title-header {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  flex-direction: row;
  align-items: top;
  justify-content: center;
  font-size: calc(18px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Page {
  width: 150vh;
  margin: 0 auto;
  background-color:  #dba6db56;
}

.Page-nav {
  min-height: 20px;
  display: flex;
  margin-bottom: 0px;
  border-bottom-style:ridge;
}
.Page-logo{
  float: left;
  padding: 10px 10px;
  height: 8vh;
  pointer-events: none;
}
.Page-nav ul {
  padding: 0;
  list-style-type: none;
  flex: 2;
  display: flex;
  
}

li {
  padding: 15px 20px 0 0;
  display: inline;
  text-align: right;
  line-height:20px;
  font-size: calc(18px + 1vmin);
  letter-spacing: 1px;
  color: white;
  flex: 1;
  cursor: pointer; 
}
.Page-empty{
  cursor: default; 
}
nav li:hover{ 
  color: rgb(137, 174, 216);  
} 


.Body1{
  display: flex;
  width: 150vh;
  margin: 0 auto;
  background-color: #dba6db56;
  border-bottom-style:ridge;
}
.Body1-main{
  color:white;
  padding: 0px 30px 30px 30px;
}
.assaultspage{
  
  width: 141vh;
}
h2 {
  font-size: 30px;
  text-align: center;
  margin: 0;
  padding: 0px 0;    
  /*text-shadow: 3px 3px 1px black;*/
}
#ass_l{
  font-size: 25px;
  text-align: center;
}
#ass_s{
  font-size: 20px;
  text-align: center;
}
.airpage p{
  font-size: 23px;
}
.airpage li{
  font-size: 23px;
  padding: 15px 20px 0 0;
  display: inline;
  text-align: right;
  line-height:60px;
  letter-spacing: 1px;
  flex: 1;
  cursor: default; 
}